@font-face {
  font-style: normal;
  font-weight: 300;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-light.woff2") format("woff2"), url("../fonts/neuemachina-light.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-regular.woff2") format("woff2"), url("../fonts/neuemachina-regular.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-medium.woff2") format("woff2"), url("../fonts/neuemachina-medium.woff") format("woff"); }

@font-face {
  font-style: normal;
  font-weight: 900;
  font-family: "Neue Machina";
  font-display: swap;
  src: url("../fonts/neuemachina-ultrabold.woff2") format("woff2"), url("../fonts/neuemachina-ultrabold.woff") format("woff"); }

*,
*::before,
*::after {
  box-sizing: border-box; }

html {
  box-sizing: border-box; }

html,
body {
  margin: 0;
  padding: 0; }

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Neue Machina", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

a {
  color: #000000;
  text-decoration: none; }

img,
video {
  display: block;
  max-width: 100%;
  height: auto; }

textarea {
  resize: none; }

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000; }

input,
textarea {
  border-radius: 0; }
  input::placeholder,
  textarea::placeholder {
    opacity: 1; }
  input:invalid,
  textarea:invalid {
    box-shadow: none; }

select {
  border-radius: 0; }

input::-ms-clear, input::-ms-reveal {
  display: none; }

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  appearance: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

select,
textarea,
input:matches([type="email"],
[type="number"],
[type="password"],
[type="search"],
[type="tel"],
[type="text"],
[type="url"]) {
  appearance: none; }

button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-height: calc(100 * var(--vh, 1vh));
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    height: calc(100 * var(--vh, 1vh));
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 35px; }
  @media (max-width: 767px) {
    .container {
      padding: 0 16px; } }
  @media (max-width: 1023px) {
    .container--no-padding {
      padding: 0; } }

@keyframes emersion-top {
  0% {
    transform: translateY(-100px); }
  100% {
    transform: translateY(0); } }

@keyframes emersion-left {
  0% {
    transform: translateX(-1000px); }
  100% {
    transform: translateX(0); } }

@keyframes emersion-right {
  0% {
    transform: translateX(1000px); }
  100% {
    transform: translateX(0); } }

@keyframes emersion-bottom {
  0% {
    transform: translateY(200px); }
  100% {
    transform: translateY(0); } }

@keyframes appear {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes scale {
  0% {
    transform: scale(1); }
  20% {
    transform: scale(0.99); }
  40% {
    transform: scale(1); }
  60% {
    transform: scale(1.01); }
  80% {
    transform: scale(1); }
  100% {
    transform: scale(0.99); } }

@keyframes bounce {
  50% {
    transform: translateY(6px); }
  100% {
    transform: translateY(-6px); } }

@keyframes spin {
  50% {
    transform: rotate(-4deg); }
  100% {
    transform: rotate(10deg); } }

.logo {
  animation: emersion-top 2s ease-out 0.5s forwards; }

.text {
  animation: emersion-left 2s ease-out 0.5s forwards; }

.text-group {
  animation: emersion-right 2s ease-out 0.5s forwards; }

.icon__dino {
  transform-origin: bottom;
  animation: spin 2s ease-in-out alternate infinite; }

.icon__zigzag {
  animation: bounce 2s ease-in-out alternate infinite; }

@media (max-width: 1023px) {
  .icon__flag {
    fill: #ffffff; }
  .icon__zigzag {
    fill: #ffffff; } }

.header {
  display: block;
  position: fixed;
  background-color: #ffffff; }
  @media (max-width: 1023px) {
    .header {
      height: 74px; } }

.header__wrapper {
  padding: 25px 0; }

.main-block {
  height: 100vh; }

.main-block__wrapper {
  display: flex;
  padding-top: 118px;
  flex-direction: row;
  align-items: flex-end; }
  @media (max-width: 1023px) {
    .main-block__wrapper {
      padding-top: 246px; } }

.main-block__title {
  width: 100%;
  max-width: 562px;
  height: 384px;
  overflow: hidden;
  font-weight: normal;
  font-size: 96px;
  line-height: 96px;
  color: #2c39f2;
  margin: 0;
  margin-right: 40px; }

.main-block__text {
  margin: 0;
  max-width: 352px;
  height: 120px;
  overflow: hidden;
  font-size: 30px;
  line-height: 40px;
  margin-top: 35px; }
  @media (max-width: 1023px) {
    .main-block__text {
      color: #ffffff; } }

@media (max-width: 1023px) {
  .main-block__dino {
    position: fixed;
    top: 74px;
    left: 0;
    right: 0;
    bottom: 0;
    width: 100%;
    z-index: 0;
    display: block;
    background-color: #2c39f2;
    transform: translateX(-100%);
    transition: transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
    padding-top: 161px; }
    .main-block__dino--show {
      transform: translateX(0);
      pointer-events: initial;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .main-block__dino--show::before {
        opacity: 1; } }

.main-block__bg {
  position: absolute;
  top: 43px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1; }
  @media (max-width: 1023px) {
    .main-block__bg {
      display: none; } }

.loader {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 200;
  background-color: #ffffff;
  overflow: hidden;
  transition: opacity 0.3s ease; }
  @media (max-width: 1023px) {
    .loader .logo {
      max-width: 100%;
      width: 550px;
      height: 70px; } }

.loader__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 20px; }

.loader__btn {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: rgba(255, 255, 255, 0);
  border: none;
  box-shadow: none;
  cursor: pointer; }
  .loader__btn:focus {
    outline: none; }

.loader__text {
  position: sticky;
  top: calc(100% - 100px);
  height: 120px;
  margin: 0;
  margin-top: 20px;
  font-size: 24px;
  line-height: 22px;
  animation: emersion-bottom 2s ease-out 0.5s forwards, scale 4s linear alternate infinite 2s; }
  @media (max-width: 767px) {
    .loader__text {
      font-size: 20px;
      line-height: 28px; } }
  .loader__text--desktop {
    display: block; }
    @media (max-width: 1023px) {
      .loader__text--desktop {
        display: none; } }
  .loader__text--mobile {
    display: none; }
    @media (max-width: 1023px) {
      .loader__text--mobile {
        display: block; } }

@media (max-width: 1023px) {
  .loader__logo svg {
    width: 550px; } }

